@import url('https://fonts.googleapis.com/css2?family=MedievalSharp&family=Roboto:wght@300;400;700&display=swap');

:root {
    --primary-color: #8B4513;
    --secondary-color: #D2691E;
    --background-color: #F5DEB3;
    --surface-color: #FAEBD7;
    --text-color: #4A2511;
    --accent-color: #DAA520;
    --error-color: #8B0000;
    --border-color: #A0522D;
    --input-background: #F5E6D3;
}

body {
    background-color: var(--background-color);
    color: var(--text-color);
    font-family: 'Roboto', sans-serif;
    background-image: url('download.jpg');
    background-repeat: repeat;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: var(--surface-color);
}

::-webkit-scrollbar-thumb {
    background: var(--border-color);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--primary-color);
}

/* Ensure all text uses the medieval font */
body, input, select, button, .stat-name, .stat-value, .modal-content h2, .preset-item span {
    font-family: 'MedievalSharp', cursive;
}

/* Tooltip Styles */
[data-tooltip] {
    position: relative;
    cursor: help;
}

[data-tooltip]::after {
    content: attr(data-tooltip);
    position: absolute;
    bottom: 125%;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 0.5rem;
    border-radius: 4px;
    font-size: 0.8rem;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
}

[data-tooltip]:hover::after {
    opacity: 1;
}